.dropdown-menu li a:focus {
  background-color: #f1f1f1;
}

/* Show dropdown when active */
.dropdown.show .dropdown-menu {
  display: block;
}

/* Mobile touch enhancements */
@media (hover: none) {
  .dropdown-toggle {
    padding: 16px 20px;
    font-size: 18px;
