    line-height: 1.85;
    font-size: 1.1rem;
    font-weight: 300;
    margin-bottom: 10px;
  }
  @media (min-width: 1200px) {
    .hidden-desktop {
      display: none;
    }
  }
  @media (max-width: 479px) {
    .hidden-mobil {
      display: none;
    }
  }
  section.section {
    padding-block: clamp(2rem, 7vw, 8rem);
  }
  section.section + section.section {
    padding-top: 0;
  }
  .flow > * + * {
    margin-top: var(--flow-spacing, 1em);
  }
  img {
    max-width: 100%;
    display: block;
  }
  .flex-group {
    display: flex;
    gap: 1rem;
  }
  .space-between {
    justify-content: space-between;
  }
  .v-center {
    align-items: end;
  }
}